async function postSendEmail(payload) {
  try {
    const res = await fetch('/api/send-email', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(payload)
    })
    const json = await res.json()
    return { ok: res.ok, status: res.status, data: json }
  } catch (err) {
    console.error('postSendEmail error', err)
    return { ok: false, error: err }
  }
}

export async function sendTaskCompletionEmail({ to, clientName, jobName, taskName, taskDescription, taskValue, completedAt }) {
  if (!to) return { ok: false, error: 'No recipient' }
  const subject = `Task finalizat: ${taskName}`
  const text = `Bun ${clientName || ''},\n\nTask-ul "${taskName}" din jobul "${jobName}" a fost marcat ca finalizat la ${completedAt || new Date().toLocaleString()}.\n\nDescriere: ${taskDescription || 'N/A'}\nValoare: ${taskValue != null ? taskValue + ' lei' : 'N/A'}\n\nMulumim!`;
  const html = `<p>Bun ${clientName || ''},</p><p>Task-ul <strong>${taskName}</strong> din jobul <strong>${jobName}</strong> a fost marcat ca finalizat la ${completedAt || new Date().toLocaleString()}.</p><p><strong>Descriere:</strong> ${taskDescription || 'N/A'}<br/><strong>Valoare:</strong> ${taskValue != null ? taskValue + ' lei' : 'N/A'}</p><p>Mulumim!</p>`
  return postSendEmail({ to, subject, text, html })
}

export async function sendJobCompletionEmail({ to, clientName, jobName, tasks = [], totalValue, completedAt }) {
  if (!to) return { ok: false, error: 'No recipient' }
  const subject = `Job finalizat: ${jobName}`
  const taskList = tasks.map(t => `- ${t.name} (${t.value ? t.value + ' lei' : 'N/A'})`).join('\n')
  const htmlTasks = tasks.map(t => `<li>${t.name}  ${t.value ? t.value + ' lei' : 'N/A'}</li>`).join('')
  const text = `Bun ${clientName || ''},\n\nJobul "${jobName}" a fost finalizat la ${completedAt || new Date().toLocaleString()}.\n\nTaskuri:\n${taskList}\n\nValoare total: ${totalValue != null ? totalValue + ' lei' : 'N/A'}`
  const html = `<p>Bun ${clientName || ''},</p><p>Jobul <strong>${jobName}</strong> a fost finalizat la ${completedAt || new Date().toLocaleString()}.</p><ul>${htmlTasks}</ul><p><strong>Valoare total:</strong> ${totalValue != null ? totalValue + ' lei' : 'N/A'}</p>`
  return postSendEmail({ to, subject, text, html })
}

export function areAllTasksCompleted(allTasks, jobId) {
  const jt = (allTasks || []).filter(t => t.job_id === jobId)
  if (jt.length === 0) return false
  return jt.every(t => t.status === 'completed')
}

const emailService = {
  sendTaskCompletionEmail,
  sendJobCompletionEmail,
  areAllTasksCompleted,
}

export default emailService
