import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import supabase from '../supabase-client';

const ProtectedRoute = ({ children, allowedRole }) => {
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(true);
  const [isAuthorized, setIsAuthorized] = useState(false);

  useEffect(() => {
    const checkAuth = async () => {
      try {
        // Pas 1: Verific dac e cineva logat
        const { data: { user }, error } = await supabase.auth.getUser();
        
        if (error || !user) {
          // Nimeni logat  napoi la Login
          navigate('/');
          return;
        }

        // Pas 2: Ia rolul din profiles
        const { data: profile } = await supabase
          .from('profiles')
          .select('role')
          .eq('id', user.id)
          .single();

        // Pas 3: Verific dac rolul corespunde
        if (profile && profile.role === allowedRole) {
          setIsAuthorized(true);
        } else {
          // Rolul nu corespunde  napoi la Login
          navigate('/');
        }
      } catch (error) {
        console.error('Error checking auth:', error);
        navigate('/');
      } finally {
        setIsLoading(false);
      }
    };

    checkAuth();
  }, [navigate, allowedRole]);

  // Arat loading n timp ce verific
  if (isLoading) {
    return <div>Loading...</div>;
  }

  // Arat componenta doar dac e autorizat
  return isAuthorized ? children : null;
};

export default ProtectedRoute;
